@import "https://fonts.googleapis.com/css2?family=Overpass&family=Red+Hat+Display:wght@500;700;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Red Hat Display, sans-serif;
}

:root {
  --pale-blue: #e0e8ff;
  --bright-blue: #3829e0;
  --very-pale-blue: #f5f7ff;
  --desaturated-blue: #7280a7;
  --dark-blue: #1f2f56;
}

body {
  background-image: url("pattern-background-desktop.f37d2923.svg");
  background-color: var(--pale-blue);
  height: 100vh;
  background-repeat: no-repeat;
  background-size: 100% 500px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.price-card-container {
  background-color: var(--very-pale-blue);
  border-radius: 20px;
  margin: 0 .8rem;
}

.price-card-container .price-content-container, .price-card-container .price-content-container .price-container, .price-card-container .buttons-container {
  display: flex;
}

.price-card-container .about-container img {
  width: 100%;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.price-card-container .about-container .about-container-content {
  text-align: center;
  margin: 0 1.5rem;
  font-size: .9rem;
}

.price-card-container .about-container .card-title {
  margin: 1rem 0;
  font-weight: 900;
}

.price-card-container .about-container .card-discription {
  color: var(--desaturated-blue);
  margin-bottom: 1rem;
  font-weight: 700;
}

.price-card-container .price-content-container {
  background-color: var(--very-pale-blue);
  filter: brightness(97%);
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  margin: 0 1rem;
  padding: .8rem;
}

.price-card-container .price-content-container a {
  border-bottom: 2px solid var(--bright-blue);
  font-size: .8rem;
  font-weight: 900;
  text-decoration: none;
}

.price-card-container .price-content-container .price-container img {
  margin-right: .8rem;
}

.price-card-container .price-content-container .price-container .price-content {
  align-self: center;
  font-size: .9rem;
}

.price-card-container .price-content-container .price-container .price-content .price-title {
  font-weight: 900;
}

.price-card-container .price-content-container .price-container .price-content .price {
  color: var(--desaturated-blue);
}

.price-card-container .buttons-container {
  flex-direction: column;
  margin: 1rem;
}

.price-card-container .buttons-container button {
  border: none;
  border-radius: 10px;
  padding: .8rem 0;
  font-weight: 900;
}

.price-card-container .buttons-container button:first-child {
  background-color: var(--bright-blue);
  color: var(--very-pale-blue);
  box-shadow: 0 4px 14px -2px #1f2f56;
}

.price-card-container .buttons-container button:first-child:hover {
  filter: brightness(150%);
  cursor: pointer;
}

.price-card-container .buttons-container button:last-child {
  color: var(--desaturated-blue);
  background-color: #0000;
}

@media only screen and (min-width: 375px) {
  .price-card-container {
    max-width: 375px;
  }
}

